#cb-ver {
  float: left;
  font-size: 9px;
  margin-top: -16px; }

#wrapper {
  padding-left: 0;
  transition: all 0.5s ease;
  overflow: hidden; }

#wrapper.toggled {
  padding-left: 43px; }

#sidebar-wrapper, #sidebar-wrapper.compact {
  z-index: 1000;
  position: fixed;
  left: 150px;
  width: 43px;
  height: 100%;
  margin-left: -150px;
  border-right: 1px solid #e7e7e7;
  background: #f8f8f8;
  transition: all 0.5s ease; }
  #sidebar-wrapper ul, #sidebar-wrapper.compact ul {
    width: 43px; }
    #sidebar-wrapper ul li a, #sidebar-wrapper.compact ul li a {
      white-space: nowrap;
      overflow: hidden; }
    #sidebar-wrapper ul .brand-label, #sidebar-wrapper.compact ul .brand-label {
      display: none; }

#workspace {
  margin-left: 43px;
  padding-right: 43px;
  width: 100%; }

/* Sidebar Styles */
.sidebar-nav {
  position: absolute;
  top: 0;
  width: 150px;
  margin: 0;
  padding: 0;
  list-style: none;
  margin-top: 2px; }

.sidebar-nav li {
  text-indent: 0px;
  line-height: 40px;
  border-bottom: 1px solid #e7e7e7;
  transition: all 0.5s ease; }

#brand {
  text-indent: 3px;
  border-bottom: 1px solid black;
  padding: 5px 0 10px 0; }
  #brand .brand-label {
    position: absolute;
    margin: 2px 0 0 -7px;
    font-weight: bolder; }

.sidebar-nav li a {
  display: block;
  text-decoration: none;
  color: #000; }

.sidebar-nav li a:hover {
  text-decoration: none;
  color: #373a3c;
  border-right: 2px solid #428BCA; }

#brand a:hover {
  color: #000;
  border-right: 0px; }

.sidebar-nav li a:active,
.sidebar-nav li a:focus {
  text-decoration: none; }

.no-margin {
  margin: 0; }

@media (min-width: 768px) {
  #wrapper {
    padding-left: 150px; }
  #sidebar-wrapper {
    width: 150px; }
    #sidebar-wrapper ul {
      width: 150px; }
      #sidebar-wrapper ul li a {
        overflow: visible; }
      #sidebar-wrapper ul .brand-label {
        display: inline; }
    #sidebar-wrapper #arr_pick_size {
      left: 150px; }
  #workspace {
    margin-left: 0;
    padding-right: 0;
    transition: all 0.5s ease; } }

#arr_pick_size {
  display: none;
  position: absolute;
  height: 41px;
  margin-left: 6px;
  width: 8.3em;
  left: 43px;
  top: 105px;
  background-color: transparent;
  unicode-bidi: bidi-override;
  direction: rtl; }
  #arr_pick_size span {
    background-color: #c0c0c0;
    margin-right: 0em;
    margin-top: 0.6em;
    width: 1em;
    height: 1em;
    display: inline-block;
    position: relative;
    border-radius: 4px; }
  #arr_pick_size span:hover {
    background-color: #afcce6; }
  #arr_pick_size span:hover ~ span {
    background-color: #afcce6; }

#sidebar-wrapper.compact #arr_pick_size {
  left: 43px; }
